.App {
  width: 100vw;
  height: 100vh;
}
body {
  margin: 0;
  padding: 0;
}

button:hover{
  cursor: pointer;
}

.Navbar {
  background-color: rgb(95, 38, 4);
  width: 100%;
  height: 150px;
  font-family: Arial, Helvetica, sans-serif;
  display: flex;

}
.Navbar .leftSide {
  flex: 50%;
  display: flex;
  justify-content: left;
  align-items: center;
}
.Navbar .leftSide .links {
  max-height: 100px;
}
.Navbar .leftSide .links a {
  text-decoration: none;
  color: cornsilk;
  font-size: 30px;
  margin-left: 25px;
}
.Navbar .rightSide {
  flex: 50%;
  display: flex;
  align-items: center;
  justify-content: flex-end;
  padding-right:  30px;
}
.rightSide input{
  width: 300px;
  height: 40px;
  border:cornsilk;
  border-radius: 4px;
  font-size: 15px;
  padding-left: 10px;
  color: black;
}
.rightSide button{
  height: 42px;
  width: 70px;
  font-size: 16px;
}
